.search-container {
    width: 100%;
    min-width: 300px;
    padding:1rem;
    font-size: 2rem;
    position: fixed;
    top: 150px;
    z-index: 999;
    background-color: lightgray;
}

.fa-search {
    color:black;
}

@media (max-width:767px) {
    .fa-search, .fa-search:focus {
        font-size:1.3rem;
    }
    .form-control:focus {
        font-size:1.3rem!important;
    }
    .search-container{
        top: 100px;
    }
    .search-container input {
        width: 80%;
        font-size:1.3rem!important;
    }
}

.search-container input {
    background: transparent;
    border: none;
    border-bottom: 3px solid gray;
    width: 45%;
    font-size: 2rem;
}

.form-control:focus {
    box-shadow: none;
    background-color: lightgray;
    font-size: 2rem;
}

.update_time{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.update_time p {
    margin-right: 20px;
    border-bottom: none;
}


