.card-list-container {
    padding: 2rem;
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 1600px;
    margin: 0 auto;
}

.card-container {
    color:black;
}

@media screen and (max-width:600px){
    .card-list-container {
        padding: 2rem 0;
    }
}

.card {
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
}

