.App {
  text-align: center;
}

.App-header {
  position: sticky;
  width: 100%;
  top:0;
  z-index: 999;
  background-color: #282c34;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  height: 150px;
}

@media (max-width:767px){
  .App-header{
    height: 100px;
    background-size: none;
  }
}
.content_cards {
  background-position: 50%;
}

.App-title {
  font-size:1.5em;
}

.App-link {
  color: #61dafb;
}

p {
  margin: .5rem 0;
  border-bottom: 1px solid gray;
  padding: .2rem 0;
}

.flex_update {
  margin-top:120px;
  min-height: 10vh;
  display:flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.box {
  margin: 1em;
  padding: 0.5em;
  border-radius: 10px;
  min-width: 200px;
}

.box.confirmed {
  padding: 0.5em;
  border: 1px solid #ce9b61;
  background: #f0bc81;
}

.box.recovered {
  padding: 0.5em;
  border: 1px solid #95c458;
  background: #c0f081;
}

.box.deaths {
  padding: 0.5em;
  border: 1px solid #e4246f;
  background: #f3053ceb;
}

@media screen and (max-widh:900px){
  .flex_update {
    flex-wrap: none;
  }
 
}

@media (max-width:767px){
  .box {
    margin: 0.1em;
    padding: 0.1em;
  }
  .box.confirmed h3, 
  .box.confirmed h4 {
    font-size:1.3rem;
  }
  .box.recovered h3, 
  .box.recovered h4 {
    font-size:1.3rem;
  }
  .box.deaths h3, 
  .box.deaths h4 {
    font-size:1.3rem;
  }
}